import React, { Component} from 'react';
let classNames = require('classnames');

export default class Controller extends Component {

    render() {
        let className = classNames('media_controller', {
            'media_controller__left': this.props.isLeft === true,
            'media_controller__right': this.props.isLeft !== true,
        });

        let classNameArrow = classNames('media_controller__arrow', {
            'media_controller__arrow_left': this.props.isLeft === true,
            'media_controller__arrow_right': this.props.isLeft !== true,
        });

        let path;

         if (this.props.isEnabled) {
            if (this.props.isLeft){
                path = "/images/main/media/arrows/left.svg"
            }

            else {
                path = "/images/main/media/arrows/right.svg"
            }
        }

        else {
             if (this.props.isLeft){
                 path = "/images/main/media/arrows/left_disabled.svg"
             }

             else {
                 path = "/images/main/media/arrows/right_disabled.svg"
             }
        }

        return (
            <div className={className} id={this.props.id}>
                <img src={path} className={classNameArrow} />
            </div>
        )
    }
}