import React, { Component} from 'react';
import Header from "../main/header/Header";
import Footer from "../main/footer/Footer";
import {Biglinks} from "../base/Biglinks";
import Space from "../base/functional/Space";
import { Links } from "../../utils/Links";

export class Contact extends Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;
        let links = new Links();

        return (
            <div className={"app"}>
                <Header handle={handleStateUpdate.bind(this)} />
                <Space />
                <Biglinks array={links.getContactArray()} />
                <Space />
                <Footer />
            </div>
        )
    }
}