import React, {Component} from "react";
import strings from "../../Localisation";
import FloatingButton from "../presentation/FloatingButton";
import nextId from "react-id-generator";
import cookie from 'react-cookies';
import MoreInfo from "../../base/functional/MoreInfo";
import {PATH_POLICY} from "../../../utils/Paths";
let classNames = require('classnames');
const GDPR_KEY = "gdpr";

export class Gdpr extends Component {

    constructor(props) {
        super(props);

        this.smallButtonId = nextId()

        let is_gdpr_ok = cookie.load(GDPR_KEY);

        this.state = {
            gdpr_ok: !!is_gdpr_ok
        }
    }

    buttonClick() {
        cookie.save(GDPR_KEY, true);
        this.setState({
            gdpr_ok: true
        })
    }

    componentDidMount() {
        let link = this;
        let button = document.getElementById(this.smallButtonId);

        button.addEventListener('click', function(event) {
            event.preventDefault();

            link.buttonClick()
        });
    }

    render() {
        let className = classNames('gdpr_popup', {
            'gdpr_popup__hidden': this.state.gdpr_ok === true
        });

        return (
            <div className={className}>
                <div className={"gdpr_popup__inside_content"}>
                    <div className={"functional__text"}>
                        { strings.gdpr_popup__text__first }
                        { <a href={PATH_POLICY}>{ strings.gdpr_popup__text__second } </a>}
                        { strings.gdpr_popup__text__third }
                    </div>

                    <div className={"gdpr_popup__button"}>
                        <MoreInfo
                            type={"close"}
                            buttonId={this.smallButtonId} />

                    </div>
                </div>
            </div>
        )
    }
}