import React, {Component} from "react";
import Text from "../../base/functional/Text";
import Title from "../../base/functional/Title";
import ShortForm from "./ShortForm";
import strings from "../../Localisation";
import Space from "../../base/functional/Space";
import {TYPE_BUSINESS, TYPE_PREORDER} from "../../../utils/FormTypes";

export default class Preorder extends Component {

    render() {
        let title = strings.footer_form__title;
        let description = strings.footer_form__description;
        let type = this.props.type? this.props.type : TYPE_PREORDER;

        if (type === TYPE_BUSINESS) {
            title = strings.business__form__title;
            description = strings.business__form__description;
        }

        return(
            <div>
                <Space />
                <div className={"functional"} id={"preorder"}>
                    <div className={"functional_left"}>
                        <Title title={title} />
                        <Text text={description} />
                        <div className={"preorder_space"}/>
                        <ShortForm type={type} />
                    </div>

                    <img className={"preorder__image"} src={"images/main/preorder/candy.png"} />
                </div>
            </div>
        )
    }
}
