import React, { Component} from 'react';
import Header from "../main/header/Header";
import strings from "../Localisation";
import Title from "../base/functional/Title";
import Text from "../base/functional/Text";
import Space from "../base/functional/Space";
import VideoFunctional from "../base/video/VideoFunctional";
import {SpeedMeasure} from "../../utils/SpeedMeasure";
import Footer from "../main/footer/Footer";
import Preorder from "../main/preorder/Preorder";
import {
    EYEHEALTH__ORGANISATIONS,
    EYEHEALTH__TIREDNESS,
    EYEHEALTH__TIREDNESS_HISTORY,
    REFACE__PROFILE
} from "../../utils/Videos";
import {TYPE_BUSINESS} from "../../utils/FormTypes";

export class Business extends Component {

    static getControlArray() {
        return [
            EYEHEALTH__TIREDNESS_HISTORY,
            EYEHEALTH__ORGANISATIONS,
            REFACE__PROFILE
        ]
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        let quality = SpeedMeasure.getResolution([
            Business.getControlArray()
        ]);

        return (
            <div className="app">
                <Header handle={handleStateUpdate.bind(this)} />

                <Space />

                <div className={"fullpage_text"}>
                    <Title title={strings.business__title} />
                    <Text text={strings.business__description} />
                </div>

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[EYEHEALTH__TIREDNESS]}
                    title={strings.eyehealth__tiredness__title}
                    description={strings.eyehealth__tiredness__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[EYEHEALTH__TIREDNESS_HISTORY]}
                    title={strings.eyehealth__tiredness_history__title}
                    description={strings.eyehealth__tiredness_history__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[EYEHEALTH__ORGANISATIONS]}
                    title={strings.eyehealth__organisations__title}
                    description={strings.eyehealth__organisations__description} />

                <Preorder type={TYPE_BUSINESS} />
                <Space />
                <Footer />
            </div>
        )
    }
}