import React, { Component} from 'react';
import Item from './Item';
import {Column} from "./Column";
import {Logo} from "./Logo";
import strings from "../../Localisation";
import Space from "../../base/functional/Space";
import RateForm from "./rate/RateForm";
import {
    Links
} from "../../../utils/Links";
import {Gdpr} from "../gdpr/Gdpr";

export default class Footer extends Component {

    getLinks(array) {
        return array.map(element =>
            <Item
                type={element.type}
                path={element.link}
                text={element.name} />
        );
    }

    render() {
        let links = new Links();

        return (
            <div>
                <Gdpr />

                <div className={"footer_space"}/>
                    <Space />
                    <div className={"footer"}>
                        <RateForm />

                        <Column title={strings.header__about}>
                            {
                                this.getLinks(links.getAboutArray())
                            }
                        </Column>

                        <Column title={strings.header__contact}>
                            {
                                this.getLinks(links.getContactArray())
                            }
                        </Column>

                        <Column title={strings.header__stuff}>
                            {
                                this.getLinks(links.getStuffArray())
                            }
                        </Column>

                        <Column title={strings.header__applications}>
                            {
                                this.getLinks(links.getApplicationsArray())
                            }
                        </Column>

                        <Logo />
                </div>

                <Space />
            </div>
        )
    }
}