import React, { Component} from 'react';
import strings from "../../Localisation";
import VideoFunctional from "../../base/video/VideoFunctional";
import Space from "../../base/functional/Space";
import {EYEHEALTH__TIREDNESS, EYEHEALTH__TIREDNESS_HISTORY} from "../../../utils/Videos";

export default class Health extends Component {

    static getHealthArray() {
        return [
            EYEHEALTH__TIREDNESS,
            EYEHEALTH__TIREDNESS_HISTORY
        ]
    }

    render() {
        return (
           <div>
               <Space />
               <VideoFunctional
                   quality={this.props.quality}
                   path={"https://www.youtube.com/watch?v=0szQHEQ7MXA"}
                   infoPath={"/eyehealth"}
                   array={Health.getHealthArray()}
                   title={strings.health__title}
                   description={strings.health__description} />
           </div>
        )
    }
}