import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import {BrowserRouter, Route} from 'react-router-dom'
import Total from './components/main/Total';
import BusinessLanding from './components/main/Business';
import ControlLanding from './components/main/Control';
import HealthLanding from './components/main/Health';

import {Screen} from './components/team/Screen'
import {About} from './components/biglinks/About'
import * as serviceWorker from './serviceWorker';
import { initAmplitude } from './utils/Amplitude';
import {Contact} from "./components/biglinks/Contact";
import {Stuff} from "./components/biglinks/Stuff";
import {Characteristics} from "./components/characteristics/Characteristics";
import {Instructions} from "./components/instructions/Instructions";
import Roadmap from "./components/roadmap/Roadmap";

import {
    PATH_MAIN,
    PATH_LANDING_BUSINESS,
    PATH_LANDING_CONTROL,
    PATH_LANDING_HEALTH,
    PATH_ABOUT,
    PATH_CHARACTERISTICS,
    PATH_CONTACT,
    PATH_INSTRUCTIONS,
    PATH_CONTROLLER,
    PATH_AGREEMENT,
    PATH_ROADMAP,
    PATH_STUFF,
    PATH_TEAM,
    PATH_POLICY,
    PATH_TRACK,
    PATH_REFACE,
    PATH_EYEHEALTH,
    PATH_BUSINESS,
    PATH_APPLICATIONS
} from "./utils/Paths";
import {Controller} from "./components/application/controller/Controller";
import Policy from "./components/policy/Policy";
import Agreement from "./components/agreement/Agreement";
import {Track} from "./components/application/track/Track";
import {Reface} from "./components/reface/Reface";
import {EyeHealth} from "./components/eyehealth/EyeHealth";
import {Business} from "./components/business/Business";
import {Applications} from "./components/biglinks/Applications";

initAmplitude();

ReactDOM.render(
    <div>
        <BrowserRouter basename={process.env.PUBLIC_URL}>
            <div>
                <Route exact path={PATH_MAIN} component={ Total } />
                <Route path={PATH_LANDING_BUSINESS} component={ BusinessLanding } />
                <Route path={PATH_LANDING_HEALTH} component={ HealthLanding } />
                <Route path={PATH_LANDING_CONTROL} component={ ControlLanding } />

                <Route path={PATH_INSTRUCTIONS} component={ Instructions } />
                <Route path={PATH_CHARACTERISTICS} component={ Characteristics } />
                <Route path={PATH_TEAM} component={ Screen } />
                <Route path={PATH_ABOUT} component={ About } />
                <Route path={PATH_CONTACT} component={ Contact } />
                <Route path={PATH_STUFF} component={ Stuff } />
                <Route path={PATH_ROADMAP} component={ Roadmap } />
                <Route path={PATH_CONTROLLER} component={ Controller } />
                <Route path={PATH_POLICY} component={ Policy } />
                <Route path={PATH_AGREEMENT} component={ Agreement } />
                <Route path={PATH_REFACE} component={ Reface } />
                <Route path={PATH_APPLICATIONS} component={ Applications } />
                <Route path={PATH_EYEHEALTH} component={ EyeHealth } />
                <Route path={PATH_BUSINESS} component={ Business } />
                <Route path={PATH_TRACK} component={ Track } />
            </div>
        </BrowserRouter>
    </div>,
    document.getElementById('root')
);
// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more biglinks service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
