import React, { Component} from 'react';
import Text from '../functional/Text';
import Title from '../functional/Title';
import MoreInfo from '../functional/MoreInfo';
import VideoPhoneContainer from "./VideoPhoneContainer";

export default class VideoFunctional extends Component {

    render() {
        return (
            <div className={"functional"}>
                <div className={"functional_left"}>
                    <Title title={this.props.title}/>
                    <Text text={this.props.description} />

                    <div className={"more_info"}>
                        {
                            (this.props.path)?
                                <MoreInfo
                                    path={this.props.path}/>
                                :
                                <div/>


                        }

                        {
                            (this.props.infoPath)?
                                <MoreInfo
                                    type={"info"}
                                    path={this.props.infoPath}/>
                                :
                                <div/>
                        }
                    </div>

                </div>

                <VideoPhoneContainer array={this.props.array} quality={this.props.quality} />
            </div>
        )
    }
}