import React, { Component} from 'react';
import nextId from "react-id-generator";

export default class VideoPhone extends Component {
    constructor(prop) {
        super(prop);

        this.phoneId = nextId();
    }

    updateVideo() {
        let videoContainer = document.getElementById(this.props.id);
        videoContainer.src = this.props.src;
        videoContainer.load();
        videoContainer.play();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        this.updateVideo();
    }

    componentDidMount() {
        this.updateVideo();
    }

    render() {
        return (
            <div id={this.phoneId} className={"functional__phone"}>
                <img
                    className={"functional__phone_container__phone_corpus"}
                    id={this.phoneId}
                    src={"/images/main/functional/phone.png"}/>

                <video
                    playsInline
                    autoPlay={true}
                    controls={false}
                    muted
                    className={"functional__phone_container__video"}
                    id={this.props.id} />
            </div>
        )
    }
}