import React, { Component} from 'react';
import Header from "../main/header/Header";
import strings from "../Localisation";
import Title from "../base/functional/Title";
import Text from "../base/functional/Text";
import Space from "../base/functional/Space";
import VideoFunctional from "../base/video/VideoFunctional";
import {SpeedMeasure} from "../../utils/SpeedMeasure";
import Footer from "../main/footer/Footer";
import Preorder from "../main/preorder/Preorder";
import {
    REFACE__COMPUTER_CONNECTION,
    REFACE__CREATED_ACTIONS,
    REFACE__CREATING_ACTION, REFACE__DIAGNOSTICS, REFACE__RETRAINING
} from "../../utils/Videos";
import Subtitle from "../base/functional/Subtitle";

export class Reface extends Component {
    static getControlArray() {
        return [
            REFACE__CREATING_ACTION,
            REFACE__CREATED_ACTIONS,
        ]
    }

    handleStateUpdate() {
        this.setState({});
    }

    render() {
        let handleStateUpdate = this.handleStateUpdate;

        let quality = SpeedMeasure.getResolution([
            Reface.getControlArray()
        ]);

        return (
            <div className="app">
                <Header handle={handleStateUpdate.bind(this)} />

                <Space />

                <div className={"fullpage_text"}>
                    <Title title={strings.control__title} />
                    <Text text={strings.control__description} />
                </div>

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={Reface.getControlArray()}
                    title={strings.reface__actions__title}
                    description={strings.reface__actions__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[REFACE__COMPUTER_CONNECTION]}
                    title={strings.reface__computer_connection__title}
                    description={strings.reface__computer_connection__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[REFACE__RETRAINING]}
                    title={strings.reface__retraining__title}
                    description={strings.reface__retraining__description} />

                <Space />

                <VideoFunctional
                    quality={quality}
                    array={[REFACE__DIAGNOSTICS]}
                    title={strings.reface__diagnostics__title}
                    description={strings.reface__diagnostics__description} />

                <Space />

                <Preorder />
                <Space />
                <Footer />
            </div>
        )
    }
}