import React, {Component} from "react";
import {Step} from "./Step";
import VideoPhoneContainer from "../video/VideoPhoneContainer";
import Title from "../functional/Title";
import Text from "../functional/Text";

export class StepFunctional extends Component {

    getSteps() {
        let link = this;

        return this.props.steps.map((item, i) =>
            <Step
                number={i + 1}
                text={item}
                isTicked={i + 1 !== link.props.steps.length && this.props.steppersEnabled} />
        )
    }

    render() {
        return (
            <div className={"functional"}>
                <div className={"functional_left"}>
                    <Title title={this.props.title} />

                    {
                        (this.props.description)?
                            <Text text={this.props.description} />
                            :
                            <div/>
                    }

                    <div className={"steps__array"}>
                        {
                            this.getSteps()
                        }
                    </div>
                </div>

                <VideoPhoneContainer
                    array={this.props.array}
                    quality={this.props.quality} />
            </div>
        )
    }
}