import React, { Component} from 'react';
import strings from "../../Localisation";
import VideoFunctional from "../../base/video/VideoFunctional";
import Space from "../../base/functional/Space";
import {EYEHEALTH__ORGANISATIONS, EYEHEALTH__TIREDNESS_HISTORY} from "../../../utils/Videos";

export class Business extends Component {

    static getBusinessArray() {
        return [
            EYEHEALTH__ORGANISATIONS,
            EYEHEALTH__TIREDNESS_HISTORY
        ]
    }

    render() {
        return (
            <div>
                <Space />
                <VideoFunctional
                    quality={this.props.quality}
                    array={Business.getBusinessArray()}
                    infoPath={"/business"}
                    title={strings.business__title}
                    description={strings.business__description} />
            </div>
        )
    }
}