import React, {Component} from "react";

export class StepText extends Component {

    render() {
        let text = this.props.text.split('\n').map((item, i) => {
            return <div className={"step_text__item"} key={i}>{item}</div>;
        });

        return (
            <div className={"step_text"}>
                {text}
            </div>
        )
    }
}